import request from '@/utils/request'

export function getList(query) {
  return request({
    url: '/admin_user/list',
    method: 'get',
    params: query
  })
}

export function getConlogList(id, query) {
  return request({
    url: '/admin_user/' + id + '/conlogs',
    method: 'get',
    params: query
  })
}

export function update(id, payload) {
  return request({
    url: '/admin_user/' + id,
    method: 'put',
    data: payload
  })
}

export function getEditInfo(id) {
  return request({
    url: '/admin_user/' + id + '/edit_info',
    method: 'get'
  })
}
