<template>
  <div class="app-container adminuser-edit">
    <div class="header">
      <div class="title">
        编辑成员信息【<span class="name">{{ name }}</span
        >】
      </div>
      <div class="avatar" :style="{ backgroundImage: `url(${form.avatar})` }" />
    </div>
    <div class="content-container">
      <el-form
        ref="form"
        :model="form"
        :rules="rules"
        label-width="100px"
        style="margin-top:110px;width:500px"
      >
        <el-form-item label="成员姓名" prop="name">
          <el-input v-model.trim="form.name" />
        </el-form-item>
        <el-form-item label="成员性别" prop="gender">
          <el-select
            v-model="form.gender"
            style="width:100%"
            placeholder="未选择"
          >
            <el-option
              v-for="item in genderArr"
              :key="item._id"
              :label="item.name"
              :value="item._id"
            />
          </el-select>
        </el-form-item>
        <el-form-item label="成员ID" prop="id">
          <el-input v-model="form.id" disabled />
        </el-form-item>
        <el-form-item label="钉钉ID" prop="userid">
          <el-input v-model="form.userid" disabled />
        </el-form-item>
        <el-form-item label="部门" prop="department">
          <el-input v-model="form.department" disabled />
        </el-form-item>
        <el-form-item label="权限组" prop="roles">
          <el-select
            v-model="form.roles"
            style="width:100%"
            placeholder="请选择"
          >
            <el-option
              v-for="item in roles"
              :key="item._id"
              :label="item.name"
              :value="item._id"
            />
          </el-select>
        </el-form-item>
        <el-form-item label="电话" prop="mobile">
          <el-input v-model="form.mobile" />
        </el-form-item>
        <el-form-item label="邮箱" prop="email">
          <el-input v-model="form.email" />
        </el-form-item>
        <el-form-item style="margin-top:60px;text-align: center;">
          <el-button
            :loading="loading"
            size="medium"
            type="primary"
            @click="submitForm('form')"
            >保存修改</el-button
          >
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import { getEditInfo, update } from '@/api/adminUser'
export default {
  name: 'AdminUserEdit',
  data() {
    var checkPhone = (rule, value, callback) => {
      const phoneReg = /^1(3|4|5|6|7|8|9)\d{9}$/
      if (!value) {
        return callback(new Error('电话号码不能为空'))
      }
      setTimeout(() => {
        // Number.isInteger是es6验证数字是否为整数的方法,但是我实际用的时候输入的数字总是识别成字符串
        // 所以我就在前面加了一个+实现隐式转换

        if (!Number.isInteger(+value)) {
          callback(new Error('请输入数字值'))
        } else {
          if (phoneReg.test(value)) {
            callback()
          } else {
            callback(new Error('电话号码格式不正确'))
          }
        }
      }, 100)
    }
    return {
      genderArr: [
        {
          _id: 1,
          name: '男'
        },
        {
          _id: 2,
          name: '女'
        }
      ],
      name: '',
      id: '',
      form: {
        name: '',
        gender: '',
        userid: '',
        id: '',
        department: '',
        roles: '',
        mobile: '',
        email: ''
      },
      formGender: '',
      loading: false,
      roles: [],
      rules: {
        name: [
          {
            required: true,
            message: '请填写成员姓名',
            trigger: 'blur'
          }
        ],
        gender: [
          {
            required: true,
            message: '请选择成员性别',
            trigger: 'blur'
          }
        ],
        userid: [
          {
            required: true,
            trigger: 'blur'
          }
        ],
        id: [
          {
            required: true,
            trigger: 'blur'
          }
        ],
        department: [
          {
            required: true,
            trigger: 'blur'
          }
        ],
        roles: [
          {
            required: true,
            message: '请选择对应的角色',
            trigger: 'change'
          }
        ],
        mobile: [
          {
            required: true,
            message: '请填写电话',
            trigger: 'blur'
          },
          { validator: checkPhone, trigger: 'blur' }
        ]
      }
    }
  },
  created() {
    this.name = this.$route.query.name
    this.id = this.$route.params.id
    if (this.id) {
      this.rules.userid = []
      this.rules.id = []
      this.rules.department = []
    }
    this.handlerGetEditInfo(this.id)
  },
  methods: {
    handlerGetEditInfo(id) {
      getEditInfo(id).then(res => {
        res.data.adminuser.gender = res.data.adminuser.gender
          ? res.data.adminuser.gender
          : ''
        this.form = res.data.adminuser
        this.roles = res.data.roles
      })
    },
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          //   alert('submit!')
          if (!this.loading) {
            this.loading = true
            const payload = {
              name: this.form.name,
              roles: this.form.roles,
              mobile: this.form.mobile,
              email: this.form.email,
              gender: this.form.gender
            }
            update(this.id, payload)
              .then(res => {
                this.loading = false
                this.$message({
                  message:
                    res && res.data ? '成员信息修改成功' : '成员信息修改失败',
                  type: res && res.data ? 'success' : 'error'
                })
                this.$router.go(-1)
              })
              .catch(err => {
                console.log(err)
                this.loading = false
              })
          }
        } else {
          return false
        }
      })
    }
  }
}
</script>

<style lang="scss">
.adminuser-edit {
  .header {
    display: flex;
    height: 40px;
    line-height: 40px;
    .title {
      font-size: 20px;
      color: #17233d;
      .name {
        line-height: 20px;
        color: #0085ff;
        letter-spacing: 0;
      }
    }
    .avatar {
      width: 40px;
      height: 40px;
      background-position: center center;
      background-size: cover;
      border-radius: 50%;
    }
  }
  .content-container {
    display: flex;
    justify-content: center;
  }
}
</style>
